html,
body {
	background-color: black;
	margin: 0;
	padding: 0;
	width: 100%;
}

html {
	scroll-behavior: smooth;
}

a.button {
	background: transparent;
	margin: 0 5px 0 5px;
	border: none;
	color: white;
	font-family: 'Russo One', sans-serif;
}

a.button:hover {
	background-color: #191919;
	color: #4cbaff;
	border: none;
	border-radius: 7px;
}

@media (max-width: 768px) {
	a.button:hover {
		border: none;
		color: #4cbaff;
	}
}

* {
	box-sizing: border-box;
}

nav {
	background: rgba(26, 26, 26, 0.8);
	position: fixed;
	width: 100%;
	display: block;
	-webkit-align-items: center;
	        align-items: center;
	height: 65px;
	z-index: 300;
}

.logo {
	float: left;
}

.nav-links {
	display: -webkit-flex;
	display: flex;
	text-transform: uppercase;
	float: right;
	margin: 10px 10px 0 0;
}

.nav-burger div {
	width: 30px;
	height: 4px;
	background-color: #4cbaff;
	margin: 7px;
}

.nav-burger {
	display: none;
	cursor: pointer;
}

@media (max-width: 768px) {
	body {
		overflow: hidden;
	}
	.nav-links {
		position: absolute;
		height: 130px;
		top: 55px;
		background: rgba(26, 26, 26, 0.8);
		width: 100%;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-align-items: center;
		        align-items: center;
		-webkit-transform: translateY(-200%);
		        transform: translateY(-200%);
	}

	.nav-burger {
		display: block;
		float: right;
		margin: 10px 10px 0 0;
	}
}

.nav-active {
	-webkit-transform: translateY(0%);
	        transform: translateY(0%);
}

h2 {
	margin: 5px;
	font-weight: 900;
}

