.modal-outer {
	display: grid;
	position: fixed;
	background: rgba(76, 186, 255, 0.3);
	height: 100vh;
	width: 100vw;
	top: 0;
	left: 0;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	opacity: 0;
	pointer-events: none;
	transition: opacity 0.3s;
	z-index: 500;
}

.modal-outer.open {
	opacity: 1;
	pointer-events: all;
}

.modal-inner {
	max-width: 1000px;
	min-width: 800px;
	min-height: 550px;
	background: #1d1d1c;
	padding: 5px;
	border-radius: 30px;
}

